import { batch } from 'react-redux'
import store from '../../../app/store'
import { setZombiesOnStage, incrementWave, setWaveCountToDefeat, decrementZombieAttackTimeLimit, incrementZombieHealth, updateZombieWalkSpeed, updateZombieAttackDmg } from '../gameSlice'
import { createWaveMessage } from '../createMessages'
import { GenerateZombies } from '../items/ItemGenerators'
import GameStates, { SubStates } from '../constants/GameStates'
import { WEAPON_TYPE_PISTOL, WEAPON_TYPE_SHOTGUN, WEAPON_TYPE_SMG } from '../constants/weapons'
import { updateScore } from '../utils/updateScore'
import { resetWave } from '../ReactComponents/PauseMenu'
import saveGame from '../utils/saveGame'

const { getState, dispatch } = store

export const startWave = () => {

  if (getState().game.waveNum === 0) {
    dispatch(incrementWave())
  }

  const start = () => {
    if (
          (
            getState().game.zombiesOnStage === 0 ||
            window.gameObjects.getKeyValue('zombies').length === 0
          )
          &&
          getState().game.gameState === GameStates.GAMEPLAY &&
          getState().game.gameSubState !== SubStates.SHOP_QUERY_SUBMENU &&
          getState().game.gameSubState !== SubStates.WAVE_DEFEATED
      ) {
      let { waveNum } = getState().game
      batch(() => {
        dispatch( updateZombieWalkSpeed( waveNum ) )
        dispatch( updateZombieAttackDmg( waveNum ) )
        dispatch( decrementZombieAttackTimeLimit( waveNum ) )
        dispatch( incrementZombieHealth( waveNum ) )
        dispatch( setWaveCountToDefeat( waveNum ) )
      })
      GenerateZombies( waveNum )
    } else {
      setTimeout(start, 3000)
    }
  }

}

const clearGameObjects = () => {
  // destroy current renderable objects 
  let { gameObjects } = window
  let credits = gameObjects.getKeyValue('currency-drops')
  let healthdrops = gameObjects.getKeyValue('health-drops')
  let pistolAmmoDrops = gameObjects.getKeyValue('ammo-drops-by-type')[WEAPON_TYPE_PISTOL]
  let smgAmmoDrops = gameObjects.getKeyValue('ammo-drops-by-type')[WEAPON_TYPE_SMG]
  let shotgunAmmoDrops = gameObjects.getKeyValue('ammo-drops-by-type')[WEAPON_TYPE_SHOTGUN]
  credits.forEach(credit => { credit.destroy() })
  pistolAmmoDrops
    .concat(smgAmmoDrops)
    .concat(shotgunAmmoDrops)
    .forEach(ammo => { ammo.destroy() })
  healthdrops.forEach(health => { health.destroy() })
}

export const waveDefeated = () => {
  dispatch( setZombiesOnStage(0) )
  dispatch( setWaveCountToDefeat(null) )
  createWaveMessage('Wave Defeated!')
  updateScore( getState().game.scoreAmounts.waveCompleted * getState().game.waveNum )
  dispatch(incrementWave())
  saveGame()
}
