import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components'
import MenuComponents from './MenuComponents'
import { CustomMenuButton, Inline } from './ShopMenu'
import { gameStateTransition, incrementWave, setScoreSubmissionName, setSubmissionForWave } from '../gameSlice'
import GameStates, { SubStates } from '../constants/GameStates'
import store from '../../../app/store'
import saveGame from '../utils/saveGame'
import Axios from 'axios'

const { MenuButton, MenuButtonContainer, MenuButtonSpacer, Container, Backing } = MenuComponents

const BottomBanner = styled.div`
height: 140px;
width: 100%;
position: absolute;
margin: auto;
top: 40px;
`

const Back = styled.div`
height: 140px;
width: 100%;
background-color: #222;
opacity: .5;
`

export const nextWave = () => {
  let { dispatch, getState } = store
  let { waveNum:wave, lvlToWin, gameState, gameSubState } = getState().game
  let screenUI = window.gameObjects.getKeyValue('screen-ui')
  if (wave < lvlToWin) {
    saveGame()
    screenUI.unpause()
    // dispatch(incrementWave())
    dispatch( gameStateTransition( 'main', gameState, GameStates.GAMEPLAY ) )
    dispatch( gameStateTransition( 'sub', gameSubState, SubStates.WAITING ) )
  } else {
    alert('You win!')
  }
}

export const processScoreInput = (callback) => {
  // console.log(`name is: ${name}`)
  const { getState, dispatch } = store
  Axios.post('http://localhost:8080/scores', { name: getState().game.scoreSubmissionName, score: getState().game.score })
  .catch(err => console.log(err))
  .then(result => {
    console.log(result)
    dispatch(
      setSubmissionForWave( getState().game.waveNum )
    )
    callback()
  })
}

export default function ShopQuery () {

  const { gameObjects } = window
  const dispatch = useDispatch()
  const wave = useSelector(state => state.game.waveNum)
  const lvlToWin = useSelector(state => state.game.lvlToWin)
  const gameState = useSelector(state => state.game.gameState)
  const gameSubState = useSelector(state => state.game.gameSubState)
  const scoreSubmissions = useSelector(state => state.game.scoreSubmissions)
  const scoreSubmissionName = useSelector(state => state.scoreSubmissionName)

  const gotoShop = () => {
    dispatch( gameStateTransition( 'main', gameState, GameStates.SHOP_MENU ) )
    dispatch( gameStateTransition( 'sub', gameSubState, SubStates.NEUTRAL ) )
  }
  
  const gotoBuildMenu = () => {
    dispatch( gameStateTransition( 'main', gameState, GameStates.BUILD_MENU ) )
    dispatch( gameStateTransition( 'sub', gameSubState, SubStates.NEUTRAL ) )
  }

  const gotoScoreSubmitScreen = () => {
    dispatch( gameStateTransition('sub', gameSubState, SubStates.SCORE_ENTRY) )
  }

  const handleNameInput = e => {
    e.preventDefault()
    dispatch( setScoreSubmissionName( e.target.value ) )
  }
  
  return (
    <>
      <BottomBanner className='shop-query-menu'>
        <Back />
        <MenuButtonContainer space-top={2}>
          <MenuButtonSpacer>
            <Inline>
              <MenuButtonSpacer padding={4}>
                <CustomMenuButton width={300} height={40} onClick={ gotoShop }>Shop</CustomMenuButton>
              </MenuButtonSpacer>
            </Inline>
            <Inline>
              <MenuButtonSpacer padding={4}>
                <CustomMenuButton width={300} height={40} onClick={ gotoBuildMenu }>Build Barriers/Traps</CustomMenuButton>
              </MenuButtonSpacer>
            </Inline>
          </MenuButtonSpacer>
          {
            gameSubState === SubStates.SCORE_ENTRY ?
              <MenuButtonSpacer>
                <Inline>
                  <MenuButtonSpacer padding={2}>
                    <input
                    value={ scoreSubmissionName }
                    onChange={ handleNameInput }
                    style={{
                      width: '200px',
                      height: '30px',
                      borderRadius: '2px',
                      border: '2px solid rgba(0, 0, 0, 0.5)',
                    }}
                    ></input>
                  </MenuButtonSpacer>
                </Inline>
                <Inline>
                  <MenuButtonSpacer padding={2}>
                    <CustomMenuButton
                    width={300} height={40}
                    onClick={() => {
                      processScoreInput(() => {
                        dispatch( gameStateTransition( 'sub', gameSubState, SubStates.SHOP_QUERY_SUBMENU ) )
                      })
                    }}>
                      Submit
                    </CustomMenuButton>
                  </MenuButtonSpacer>
                </Inline>
              </MenuButtonSpacer>
            : 
              <MenuButtonSpacer>
                <Inline>
                  <MenuButtonSpacer padding={2}>
                    <CustomMenuButton width={300} height={40}
                    onClick={
                      wave < lvlToWin ?
                      nextWave :
                        !scoreSubmissions[wave] || scoreSubmissions[wave] !== 1 ?
                          gotoScoreSubmitScreen : nextWave
                      }>
                      {
                        wave < lvlToWin ?
                        'Next Wave' :
                          !scoreSubmissions[wave] || scoreSubmissions[wave] !== 1 ?
                            'Submit your score!' : 'Next Wave'
                      }
                    </CustomMenuButton>
                  </MenuButtonSpacer>
                </Inline>
                {
                  (!scoreSubmissions[wave] || scoreSubmissions[wave] !== 1) && wave < lvlToWin ?
                  <Inline>
                    <MenuButtonSpacer padding={2}>
                      <CustomMenuButton width={300} height={40} onClick={ gotoScoreSubmitScreen }>
                        Submit your score
                      </CustomMenuButton>
                    </MenuButtonSpacer>
                  </Inline>
                  :
                  <></>
                }
              </MenuButtonSpacer>
          }
        </MenuButtonContainer>
      </BottomBanner>
  </>
  )

}